@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css"; //unminify
*{margin: 0;padding: 0;box-sizing: border-box;}
ul{list-style: none;}
input{
    all: unset;/* 기본값,디자인적 요소들 초기화_테두리 안보이게 해줌 */
    box-sizing: border-box;
    appearance: none;/* home에 있는 구성요소 없애줌 */
}
body{
    background-color: #000;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 
    Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 14px;color: #fff;
}
button{background-color: #fff;color: #000;}
a{text-decoration: none;color: inherit;}
form{width: 100%;}
.factoryInput__input::placeholder{color: #fff;opacity: 0.9;}
.container{
    width: 100%;max-width: 320px;
    display: flex;flex-direction: column;
    margin: 0 auto;margin-top: 80px;
}
.formInput{
    width: 100%;
    padding: 10px 20px;
    border-radius: 20px;
    border: 1px solid #000;
    text-align: center;
    background-color: #fff;
    color: #000;
}
.formBtn{
    cursor: pointer;
    width: 100%;
    padding: 7px 20px;
    background-color: #04aaff;
    color: #fff;
    border-radius: 20px;
    text-align: center;
}
.cancelBtn{cursor: pointer;background-color: tomato;}